/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */
.wp-block-formello-form fieldset {
  padding: 10px;
  border: 1px solid black;
}
.wp-block-formello-form select {
  max-width: inherit;
}
.wp-block-formello-form input:disabled, .wp-block-formello-form input.disabled, .wp-block-formello-form select:disabled, .wp-block-formello-form select.disabled, .wp-block-formello-form textarea:disabled, .wp-block-formello-form textarea.disabled {
  background-color: #fff;
  border: 1px solid #7e8993;
  opacity: 1;
}
.wp-block-formello-form .block-editor-block-list__block {
  margin: 5px 0;
}
.wp-block-formello-form .formello {
  padding: 10px 0;
}
.wp-block-formello-form .formello small {
  padding: 0;
}

.formello-checkbox input {
  margin: 0;
}

.formello-hidden {
  display: flex;
  align-items: center;
}

.wp-block-formello-button .button-span {
  background: #abb8c3;
  color: #fff;
  border: 1px solid transparent;
  cursor: pointer;
  padding: 10px 20px;
  min-width: 80px;
  align-self: flex-start;
}
.wp-block-formello-button .button-span.center {
  align-self: center;
}
.wp-block-formello-button .button-span.right {
  align-self: flex-end;
}
.wp-block-formello-button .button-span.wide {
  align-self: auto;
}

.formello-group div {
  display: flex;
  flex: 1 0 auto;
  align-items: flex-end;
}
.formello-group div input {
  height: 100%;
}
.formello-group div [data-type="formello/button"] {
  flex: 0 1 auto;
}
.formello-group div.formello {
  padding: 0;
}

.cta {
  cursor: pointer;
  border: 1px solid;
}

.mailchimp {
  height: 50px;
  background-image: url(../assets/images/mailchimp.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}